import React, {useState} from 'react';
import Navbar from '../components/Navbar';
import Footer from '../components/Footer';
import styled from 'styled-components';
import CartItem from '../components/CartItem';
import {shoppingCartItems} from '../data';
import PriceTotal from '../components/PriceTotal'


const Container = styled.div`

`;
const Wrapper = styled.div`
    display: flex;
    justify-content: space-between;
    min-height: 100vh;
`;

const ProductContainer = styled.div`
    flex: 2;
    padding: 0px 60px;
`
const PriceContainer = styled.div`
    flex: 1;
    padding: 50px;
    display: flex;
    justify-content: center;
    height: fit-content;
    align-items: center;
    flex-direction: column;
`
const Title = styled.h1`
    font-size: 50px;
    margin-bottom: 30px;
`

const Description = styled.div`
    display: flex;
    justify-content: space-between;
    padding-left: 270px;
    padding-right: 30px;
`;
const DescriptionItem = styled.p`
   
`;

const CartItemsContainer = styled.div`
    width: 100%;
`;

const Button = styled.button`
    width: 100%;
    margin: 20px 0px 0px;
    font-size: 30px;
    padding: 20px;
    background-color: transparent;
    transition: all 0.3s ease-in;
    cursor: pointer;
    &:hover{
        color: white;
        background-color: #191919;
    }
`;


const Cart = () => {
    const [cartData, setCart] = useState(shoppingCartItems);
    const [state, updateState] = React.useState();
    const forceUpdate = React.useCallback(() => updateState({}), []);

    const onDelete = (product) => {
        removeProductFromList(product);
        setCart(shoppingCartItems);
        forceUpdate();
    }

    const onUpdate = () => {
        setCart(shoppingCartItems);
        forceUpdate();
    }

    const removeProductFromList = (product) => {
        let index = shoppingCartItems.indexOf(product);
        if(index > -1){
            shoppingCartItems.splice(index, 1);
        }
    } 

    return (
        <Container>
            <Navbar/>
            <Wrapper>
                <ProductContainer>
                    <Title>TU CARRITO</Title>
                    <CartItemsContainer>
                        <Description>
                            <DescriptionItem>
                                ITEM
                            </DescriptionItem>
                            <DescriptionItem>
                                SUBTOTAL
                            </DescriptionItem>
                        </Description>
                        {cartData.map(item => 
                            <CartItem product={item} key={item.id} deleteItem={() => onDelete(item)}  update={()=>onUpdate()}/>
                        )}
                    </CartItemsContainer>
                    
                </ProductContainer>
                <PriceContainer>
                    <PriceTotal />
                    <Button>COMPLETAR ORDEN</Button>
                </PriceContainer>

                
            </Wrapper>
            <Footer />
        </Container>
    )
}


export default Cart
