import React from 'react';
import Home from "./pages/Home";
import ProductList from './pages/ProductList'
import Product from "./pages/SingleProduct"
import Cart from "./pages/Cart"
import {BrowserRouter as Router,Routes,Route} from "react-router-dom";
import {popularProducts, categories} from "./data";
import ScrollToTop from "./ScrollToTop";


const App = () => {
  return(
    <Router>
        <ScrollToTop />
        <Routes>
          
          {categories.map(c =>
            <Route path={`/Products/${c.id}`} key={c.id} element={<ProductList products={c.productList} title={c.title}/>}></Route>
    
          )}

          {popularProducts.map(product =>
            <Route path={`/SingleProduct/${product.id}`} key={product.id} element={<Product product={product} /> }/>
          )}


          {categories.map(c =>
            c.productList.map(product => <Route path={`/SingleProduct/${product.id}`} key={product.id} element={<Product product={product} /> }/>)
          )}
            
         
          
          <Route path="/cart" element={<Cart/>}/>
          <Route path="/" element={<Home/>}/>
        </Routes>
    </Router>
  );
};

export default App;